.skills_container {
    grid-template-columns: repeat(2, 350px);
    column-gap: 3rem;
    justify-content: center;
}

.skills_content {
    background-color: var(--container-color);
    border: none;
    padding: 2rem 4rem;
    border-radius: 1.25rem;
    box-shadow: 25px 25px 50px rgba(2, 2, 2, 0.25),
    -10px -10px 30px rgba(51, 51, 51, 0.1),
    inset -5px -5px 15px rgba(78, 78, 78, 0.5);
    transition: 0.5s;
}

.skills_content:hover {
    transform: scale(1.05);
}

.skills_title {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    text-align: center;
    margin-bottom: var(--mb-1-5);
}

.skills_box {
    display: flex;
    justify-content: center;
    column-gap: 2.5rem;
}

.skills_group {
    display: grid;
    align-items: flex-start;
    row-gap: 1rem;
}

.skills_data {
    display: flex;
    column-gap: 0.5rem;
}

.skills .bx-badge-check {
    font-size: 1rem;
    color: var(--title-color);
}

.skills_name {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    line-height: 18px;
}

.skills_level, .skills_subtitle {
    font-size: var(--tiny-font-size);
    font-weight: lighter;
    color: var(--text-color);
}

.progress-bar{
    background: linear-gradient(45deg, #4158D0 0%, #C850C0 46%, #f5b641 100%);
    animation: progress 0.9s ease-in-out forwards;
    animation-delay: 0.3s;
    opacity: 0;
}

@keyframes progress {
    0%{
        width: 0;
        opacity: 1;
    }
    100%{
        opacity: 1;
    }
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    /* .skills_container {
        grid-template-columns: max-content;
        row-gap: 2rem;
        
    } */


}

/* For medium devices */
@media screen and (max-width: 768px) {
    .skills_container {
        grid-template-columns: 1fr;
    }
    .skills_content {
        padding: 1.5rem;
    }
}

/* For small devices */
@media screen and (max-width: 350px) {
    .skills_box {
        column-gap: 1.25rem;
    }

    .skills_name {
        font-size: var(--small-font-size);
    }
}